.App {
  text-align: center;
  margin: auto;
}

.player_input {
  display: inline-block;
}

.player-submit:hover {
  cursor: pointer;
  border: 1px solid lightgray;
  color: lightgray;

}

#new-game-submit, #join-game-submit{
  height: 50px;
  border-radius: 30px;
  padding: 0;
  text-align: center;
  font-weight: bold;
  vertical-align: center;
}

.Rules h1 {
  text-shadow: none;
}

#join-submit {
  height: 38px;
  width: 50px;
  margin: 10px;
}

#add-submit {
  height: 38px;
  width: 50px;
  margin: 10px;
}


