.wrapper {
  display: flex;
  flex-direction: column;
}

.app-submit {
  outline: none;
  border: 1px solid white;
  border-radius: 20px;
  font-family: Quicksand;
  text-shadow: 2px 3px 3px rgba(0,0,0, .25);
  box-shadow: 2px 3px 3px rgba(0,0,0, .25);
  background: transparent;
  color: white;
  padding: 10px 0 0 20px;
  text-align: center;
  margin: 30px auto 10px auto;
  font-size: 14pt;
  height: 70px;
  width: 200px;

}

input[type="submit"]:hover {
  cursor: pointer;
}

.switch {
  margin: auto;
}

.live {
  margin: 10px;
}

